 
// Importing modules
import React, { lazy, Suspense } from "react";
import { useSelector } from "react-redux";
import { createBrowserRouter, RouterProvider, redirect } from "react-router-dom";

import RootLayout from "./components/Pages/RootLayout";
import DefaultLayout from "./components/Pages/DefaultLayout";
import Login from "./components/Pages/Login/Login";
import Logout, { action as logoutAction } from "./components/Pages/Logout";
import ForceResetLayout from "./components/Pages/ForcedResetLayout";
import ForcedResetPassword from "./components/Pages/ForcedReset/ForcedResetPassword";
import ForgotPassword from "./components/Pages/ForgotPassword/ForgotPassword";
import Blocked from "./components/Pages/Blocked";
import ErrorPage from "./components/Pages/Error";
import DefaultErrorPage from "./components/Pages/DefaultError";
import StatusRoot from "./components/Pages/Status/StatusRoot";
import Status from "./components/Pages/Status/Status";
import JobsRoot from "./components/Pages/Jobs/JobsRoot";
import Jobs, { loader as jobsLoader } from "./components/Pages/Jobs/Jobs";
import JobDetail, { loader as jobDetailLoader } from "./components/Pages/Jobs/JobDetail";
import Budgets from "./components/Pages/Budgets/Budgets";
import ResourcesRoot from "./components/Pages/Resources/ResourcesRoot"; 
import Resources, { loader as resourcesLoader } from "./components/Pages/Resources/Resources";
import ResourceDetail from "./components/Pages/Resources/ResourceDetail";
import Settings from "./components/Pages/Settings/Settings";
import {
  authenticationLoader,
  checkAuthentication,
  tokenLoader,
  checkTokenLoader
} from "./components/utils/auth";

import TokensRootLayout from "./components/Pages/Tokens/TokensRoot";
import Tokens, {
  loader as tokensLoader,
} from "./components/Pages/Tokens/Tokens";
//import TokenDetail, { loader as tokenDetailLoader, action as revokeTokenAction} from "./components/Pages/Tokens/TokenDetail";


import './App.scss';

// lazy loading
//const Tokens = lazy(() => import("./components/Pages/Tokens/Tokens"));
//const Jobs = lazy(() => import("./components/Pages/Jobs/Jobs"));


function App() {
  

	const router = createBrowserRouter([
    {
      path: "/",
      element: <RootLayout />,
      id: "home",
      loader: tokenLoader,
      children: [
        {
          path: "forced_reset_password",
          element: <ForceResetLayout />,
          errorElement: <ErrorPage />,
          loader: checkTokenLoader,
          children: [
            {
              index: true,
              element: <ForcedResetPassword />,
            },
          ],
        },

        {
          path: "status",
          element: <StatusRoot />,
          errorElement: <ErrorPage />,
          id: "status",
          loader: checkTokenLoader,
          children: [
            {
              index: true,
              element: <Status />,
            },
          ],
        },
        {
          path: "tokens",
          element: <TokensRootLayout />,
          errorElement: <ErrorPage />,
          loader: checkTokenLoader,
          children: [
            {
              index: true,
              element: <Tokens />,
              loader: tokensLoader,
            },
          ],
        },
        {
          path: "jobs",
          element: <JobsRoot />,
          errorElement: <ErrorPage />,
          loader: checkTokenLoader,
          children: [
            {
              index: true,
              element: <Jobs />,
              loader: jobsLoader,
            },
            {
              path: ":jobId",
              id: "job-detail",
              children: [
                {
                  index: true,
                  element: <JobDetail />,
                  loader: jobDetailLoader,
                },
              ],
            },
          ],
        },
        {
          path: "budgets",
          element: <Budgets />,
          errorElement: <ErrorPage />,
          loader: checkTokenLoader,
        },
        {
          path: "resources",
          element: <ResourcesRoot />,
          errorElement: <ErrorPage />,
          loader: checkTokenLoader,
          children: [
            {
              index: true,
              element: <Resources />,
              loader: resourcesLoader,
            },
            {
              path: ":resourceId",
              id: "resource-detail",
              children: [
                {
                  index: true,
                  element: <ResourceDetail />,
                },
              ],
            },
          ],
        },
        {
          path: "settings",
          element: <Settings />,
          errorElement: <ErrorPage />,
          loader: checkTokenLoader,
        },
      ],
    },
    {
      path: "/",
      element: <DefaultLayout />,
      errorElement: <DefaultErrorPage />,
      children: [
        { index: true, element: <Login /> },
        {
          path: "login",
          element: <Login />,
          //action: loginAction,
        },
        {
          path: "logout",
          element: <Logout />,
          action: logoutAction,
        },
        {
          path: "blocked",
          element: <Blocked />,
        },
        {
          path: "forgot_password",
          element: <ForgotPassword />,
        },
      ],
    },
  ]);
 

  	return <RouterProvider router={router} />;  
        
}

export default App;
