import React, { useEffect, useContext, Suspense, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Outlet, useLoaderData, useSubmit, defer, Await, redirect } from "react-router-dom";

import TopBar from "../PageLayout/TopBar/TopBar";
import MainNavigation from "../PageLayout/MainNavigation/MainNavigation";
import Footer from "../PageLayout/Footer/Footer";
import UnauthoriedModal from "./UnauthoriedModal";

import { authActions } from "../../store/auth-slice";
import { getTokenDuration } from "../utils/auth";

const RootLayout = () => {

  const dispatch = useDispatch();
  const submit = useSubmit();

  const darkmode = useSelector((state) => state.darkmode.darkmode);
  const token = useLoaderData();
  const [expired, setExpired] = useState(false);

  useEffect(() => {
    if (!token) { return; }
    if (token === "EXPIRED") {
      //submit(null, { action: "/logout", method: "post" });
      //dispatch(authActions.logout());
      setExpired(true);
      return;
    }
    
    const tokenDuration = getTokenDuration();
    setTimeout(() => {
      //submit(null, { action: "/logout", method: "post" });
      setExpired(true);
    }, tokenDuration);
    
  }, [token]);


  return (
    <React.Fragment>
      {expired && <UnauthoriedModal />}
      
        <div className="fluid-container">
          <div className="row mx-0">
            {token && (
              <div className="col-12 col-lg-2 col-xl-2 px-0 left_sidebar_wrap">
                <MainNavigation />
              </div>
            )}
            <div
              className={`col-12 col-lg-10 col-xl-10 px-0 right_content ${
                darkmode ? "darkgrey_bg" : "lightgrey_bg"
              }`}
            >
              <div className="fluid-container ">
                <div className="row mx-0">
                  <TopBar />
                </div>
                {token && (
                  <div className={`row mx-0 main_content_container `}>
                    <Outlet />
                  </div>
                )}
                <Footer />
              </div>
            </div>
          </div>
        </div>
     
    </React.Fragment>
  );
}

export default RootLayout;
