import React from "react";
import { Link } from "react-router-dom";
import ContentCard from "../../UI/Card/ContentCard";


function JobsList(props) {
  // console.log("Jobs:");
  // console.log(props);

  let status_bg = "";

  if (props.status === "RUNNING") {
    status_bg = "running_bg";
  }
  if (props.status === "PENDING" || props.status === "WAITING") {
    status_bg = "pending_bg";
  }
  if (props.status === "CANCELED") {
    status_bg = "canceled_bg";
  }
  if (props.status === "COMPLETE") {
    status_bg = "complete_bg";
  }

    return (
      <React.Fragment>
        <div className="jobsList_wrap_fix">
          <div className="jobsList_wrap_auto">
            <ul className=" jobsList">
              <li className="job_item_row job_row_header">
                <div className={`job_column job_view ${status_bg}`}></div>
                <div className="job_column job_id">ID</div>
                <div className="job_column job_status">Status</div>
                <div className="job_column job_shots">Shots</div>
                <div className="job_column job_circuit">Circuit</div>
                <div className="job_column job_circuit_format">
                  Circuit Format
                </div>
                <div className="job_column job_target">
                  Target Specification
                </div>
                <div className="job_column job_note">Note</div>
              </li>

              {props.jobs.map((job) => (
                <li className="job_item_row" key={job.id} id={`${job.id}`}>
                  <Link to={`/jobs/${job.id}`}>
                    <div className={`job_column job_view ${status_bg}`}>
                      <span className="view_icon"></span>
                    </div>
                    <div className={`job_column job_id ${status_bg}`}>
                      {job.id}
                    </div>
                    <div className={`job_column job_status ${status_bg}`}>
                      {job.status}
                    </div>
                    <div className={`job_column job_shots ${status_bg}`}>
                      {job.shots}
                    </div>
                    <div className={`job_column job_circuit ${status_bg}`}>
                      {job.circuit}
                    </div>
                    <div
                      className={`job_column job_circuit_format ${status_bg}`}
                    >
                      {job.circuit_format}
                    </div>
                    <div className={`job_column job_target ${status_bg}`}>
                      {job.target_specification}
                    </div>
                    <div className={`job_column job_note ${status_bg}`}>
                      {job.note}
                    </div>
                  </Link>
                </li>
              ))}
            </ul>
          </div>
        </div>
      </React.Fragment>
    );
}

export default JobsList;
